import { AnimationController } from '@ionic/angular';

let IS_FADING = false;
const DURATION = 400;
const FADE = async function (baseElement: HTMLElement, direction: string = 'in') {

    if (IS_FADING) return;
    IS_FADING = true;

    const from = (direction === "in") ? 0 : 1;
    const to = (direction === "in") ? 1 : 0;
    const ease = (direction === "in") ? "ease-in" : "ease-out";
    const animationController = new AnimationController();
    const fade = animationController.create()
        .addElement(baseElement)
        .duration(DURATION)
        .iterations(1)
        .easing(ease)
        .fromTo('opacity', from, to);

    await fade.play();

    IS_FADING = false;
}




export const fadeOut = function (baseElement: HTMLElement) {
    return FADE(baseElement, 'out')
}

export const fadeIn = function (baseElement: HTMLElement) {
    return FADE(baseElement)
}