import { Injectable } from "@angular/core";
import { DataService } from "../services/data.service";
import { HttpClient } from "@angular/common/http";
import { manyToMany } from "./utils/many-to-many";

const model: string = "categories";
const relationships: any = {
  ingredients: manyToMany("categories", "ingredients"),
  recipes: manyToMany("categories", "recipes")
};

@Injectable({ providedIn: "root" })
export class CategoriesModel extends DataService {
  constructor(protected http: HttpClient) {
    super(http, model, relationships);
  }
}
