import { Component, OnInit, Input, EventEmitter, Output } from "@angular/core";

@Component({
  selector: "app-nutritional-value-display",
  templateUrl: "./nutritional-value-display.component.html",
  styleUrls: ["./nutritional-value-display.component.scss"]
})
export class NutritionalValueDisplayComponent implements OnInit {
  @Input() hasHactionButton: boolean = false;
  @Input() nutritionalValue: any = {
    kcals: 0,
    proteins: 0,
    carbs: 0,
    fats: 0
  };
  @Input() classes: any = "";
  @Output() onAction = new EventEmitter();

  get macros() {
    let carbs: any = 0;
    let proteins: any = 0;
    let fats: any = 0;

    if (this.nutritionalValue.kcals) {
      carbs =
        Math.round(
          (this.nutritionalValue.carbs * 4 * 100) / this.nutritionalValue.kcals
        ) + "%";
      proteins =
        Math.round(
          (this.nutritionalValue.proteins * 4 * 100) /
            this.nutritionalValue.kcals
        ) + "%";
      fats =
        Math.round(
          (this.nutritionalValue.fats * 9 * 100) / this.nutritionalValue.kcals
        ) + "%";
    }

    return { carbs, proteins, fats };
  }

  action() {
    this.onAction.emit();
  }
  constructor() {}

  ngOnInit() {}
}
