import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-card-advertisement',
  templateUrl: './card-advertisement.component.html',
  styleUrls: ['./card-advertisement.component.scss'],
})
export class CardAdvertisementComponent implements OnInit {
  @Input() advertisement: any = {}
  constructor() { }

  ngOnInit() { }
  goToUrl() {
    if (this.advertisement.url) {
      window.open(this.advertisement.url, '_system');
    }
  }
}
