
import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import { EventsService } from './events.service';

@Injectable({
  providedIn: 'root'
})

/**
 * A storage service that will be extended to store several data instances on the browser
 * @version: 1.0.0
 */

export class StorageService {

  // storage table ( or key as you prefer to name it, I personally look at it like a database )
  table: string = "";

  /**
   *
   * @param storage
   * @param table
   */

  constructor(protected storage: Storage, protected events: EventsService) {

  }


  /**
   * Broadcast storage state change
   * @param update
   * @param data
   */
  public _broadcast(update: string = "", data: any) {
    update = update ? update : ""
    this.events.publish(`storage:${this.table}`);
    this.events.publish(`storage:${this.table}:${update}`, data);
  }

  /**
   * Function that handles when we want to update one item of the whole items array
   * @param item
   * @param onMatch
   */

  public async _updateSingleItemState(item, onMatch?: string) {
    onMatch = onMatch ? onMatch : "update";
    const key = this.table;
    const items = await this.storage.get(key);

    if (!items || items.length === 0) {
      return null;
    }
    let _items = [];

    for (let i of items) {
      if (i.id === item.id) {
        if (onMatch === "update") {
          _items.push(item);
        }
        if (onMatch === "delete") continue;
      } else {
        _items.push(i);
      }
    }

    return this.storage.set(key, _items);
  }

  /**
   * List all events on this storage
   */
  async listEvents(): Promise<any> {
    const events = [`storage:${this.table}`]
    const actions = ['create', 'update', 'delete'];

    actions.forEach(action => {
      events.push(`storage:${this.table}:${action}`)
    });

    return events;
  }

  /**
   * Pushes a item to this storage
   * @param item
   * @async
   */
  async create(item: any): Promise<any> {
    let result;
    const key = this.table;
    const items = await this.storage.get(key);

    if (items) {
      items.push(item)
      result = await this.storage.set(key, items);
    } else {
      result = await this.storage.set(key, [item]);
    }

    this._broadcast('create', result);
    return result;
  }

  /**
   * Gets all items from a table
   */
  async read(): Promise<any> {
    return await this.storage.get(this.table) || [];
  }

  /**
   * Updates a item
   * @param item
   */
  async update(item: any): Promise<any> {
    const result = await this._updateSingleItemState(item, 'update');
    this._broadcast('update', item);
    return result;
  }

  /**
   * Remove item
   * @param id
   */
  async destroy(id: number): Promise<any> {
    const item = { id };
    const result = this._updateSingleItemState(item, 'delete');
    this._broadcast('delete', item);
    return result;
  }
}
