import { Injectable } from "@angular/core";
import { DataService } from "../services/data.service";
import { HttpClient } from "@angular/common/http";
import { manyToMany } from "./utils/many-to-many";

const model: string = "recipes";
const relationships: any = {
    ingredients: manyToMany("recipes", "ingredients"),
    categories: manyToMany("recipes", "categories")
};

@Injectable({ providedIn: "root" })
export class RecipesModel extends DataService {
    constructor(protected http: HttpClient) {
        super(http, model, relationships);
    }
}
