import { Injectable } from "@angular/core";
import { DataService } from "../services/data.service";
import { HttpClient } from "@angular/common/http";
import { hasOne } from "./utils/has-one";

const model: string = "prices";
const relationships: any = {
  currency: hasOne("currencies", "currencyId"),
  store: hasOne("stores", "storeId"),
  ingredient: hasOne("ingredients", "ingredientId")
};

@Injectable({ providedIn: "root" })
export class PricesModel extends DataService {
  constructor(protected http: HttpClient) {
    super(http, model, relationships);
  }
}
