import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { HttpHeaders } from "@angular/common/http";

const OPTIONS = function () {
  let headers = {
    "Content-Type": "application/json"
  };
  if (TOKEN) {
    headers["Authorization"] = `Bearer ${TOKEN}`;
  }
  const httpOptions = {
    headers: new HttpHeaders(headers)
  };

  return httpOptions;
};

const URL: string = "https://pihhs-recipe-calculator-databa.herokuapp.com/";
const URL_V2: string = "https://fulgurant-spins.000webhostapp.com/wp-json/wp/v2/";

const queryBuilderV2 = function(config: any = {}){
  let url = URL_V2;
  const endpoint = config.endpoint;
  const id = config.id;
  const relationships= config.relationships;
  delete config.endpoint;
  delete config.id;
  delete config.relationships

  if(!endpoint)throw "Need a rest api endpoint";
  url +=endpoint;
  if(id){
    url+= '/'+id;

  }else{
    url +="?";
    for(let key of Object.keys(config)){
      url += key+"="+config[key]+"&";
    }
  }
    return url;

}

const queryBuilder = function (config: any = {}) {
  let url = URL;
  if (config.forceUrl) {
    return url + config.forceUrl;
  }
  url += config.model;
  if (config.id) {
    url += `/${config.id}`;
    delete config.id;
  }
  delete config.model;
  const keys = Object.keys(config);
  if (keys.length > 0) url += "?filter={";
  if (config.limit) url += `"limit":${config.limit},`;
  if (config.skip) url += `"skip":${config.skip},`;
  if (config.order) url += `"order":"${config.order}",`;
  if (config.search) url += `"where":{"name":{"regexp":"/${config.search}/i"}}`;
  url = url.replace(/,\s*$/, "");
  if (keys.length > 0) url += "}"

  if (url.charAt(url.length - 1) == "&") {
    url = url.substr(0, url.length - 1);
  }
  return url;
};

let TOKEN = "";

export class DataService {
  relationships: any = [];
  model: any = "";
  constructor(
    protected http: HttpClient,
    model: any = "",
    relationships: any = []
  ) {
    this.model = model;
    this.relationships = relationships;
  }

  protected _setToken(token: string = "") {
    TOKEN = token;
  }

  public _getToken() {
    return TOKEN;
  }

  public _handleException(config: any = {}, ex: any = {}) {
    console.log("EX", { config, ex });
    if (!config.skipError) {
      // alert("Http Request failed");
    }
    return Promise.reject(ex.error.error);
  }

  public _parseConfig(config: any = {}, id: any = 0) {
    if (id) config.id = id;
    config.model = this.model;
    return config;
  }

  public async _addImages(result: any = {}) {
    if (Array.isArray(result)) {
      result.forEach(r => {
        if (r.thumbnail) r.thumbnail = URL + r.thumbnail;
        if (r.avatar) r.avatar = URL + r.avatar;
      })
    } else {
      if (result.thumbnail) result.thumbnail = URL + result.thumbnail;
      if (result.avatar) result.avatar = URL + result.avatar;
    }

    return result;
  }

  public async _addRelations(result: any = {}, config: any = {}) {
    if (Array.isArray(config.include)) {
      // Checks for relationship and updates the
      if (Array.isArray(result)) {
        for (let r of result) {
          for (let relation of config.include) {
            if (this.relationships && this.relationships[relation]) {
              if (this.relationships[relation].type === "manyToMany") {
                this._addManyToManyRelation(r, relation);
              }
              if (this.relationships[relation].type === "hasOne") {
                this._addHasOneRelationship(r, relation);
              }
            }
          }
        }
      } else {
        for (let relation of config.include) {
          if (this.relationships && this.relationships[relation]) {
            if (this.relationships[relation].type === "manyToMany") {
              this._addManyToManyRelation(result, relation);
            }
          }
          if (this.relationships[relation].type === "hasOne") {
            this._addHasOneRelationship(result, relation);
          }
        }
      }
    }
  }

  public async _addManyToManyRelation(result: any, relation: string) {
    result[relation] = [];
    result[relation] = this.get({
      forceUrl: this.relationships[relation].url(result)
    }).then(rels => {
      result[relation] = rels;
    });
  }

  public _addHasOneRelationship(result: any, relation: string) {
    // result[relation] = await this.get({
    //   forceUrl: this.relationships[relation].url(result)
    // });
    result[relation] = {};
    this.get({
      forceUrl: this.relationships[relation].url(result)
    }).then(rel => {
      result[relation] = rel;
    });
  }

  async find(id: number = 0, config: any = {}) {
    try {
      config = this._parseConfig(config, id);
      const result = await this.http
        .get(queryBuilder(config), OPTIONS())
        .toPromise();

      this._addRelations(result, config);
      this._addImages(result);
      return result;
    } catch (ex) {
      return this._handleException(config, ex);
    }
  }

  async get(config: any = {}) {
    try {
      if (!config.order) config.order = "id DESC";
      config = this._parseConfig(config);

      const result = await this.http
        .get(queryBuilder(config), OPTIONS())
        .toPromise();

      this._addRelations(result, config);
      this._addImages(result);
      return result;
    } catch (ex) {
      return this._handleException(config, ex);
    }
  }

  async create(data: any = {}, config: any = {}) {
    try {
      config = this._parseConfig(config);
      const result = await this.http
        .post(queryBuilder(config), data, OPTIONS())
        .toPromise();

      return result;
    } catch (ex) {
      return this._handleException(config, ex);
    }
  }

  update(id: number = 0, data: any = {}, config: any = {}) {
    console.log("patch", id, data, config, this);
  }

  async delete(id: number = 0, config: any = {}) {
    try {
      config = this._parseConfig(config, id);
      const result = await this.http
        .delete(queryBuilder(config), OPTIONS())
        .toPromise();
      return result;
    } catch (ex) {
      return this._handleException(config, ex);
    }
  }

  async v2GetRecipes(config:any = {}){
    config.endpoint = "wprm_recipe";
    const result = await this.http
      .get(queryBuilderV2(config))
      .toPromise();
    return result;
  }
}
