import { Component, Input, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-card-ingredient',
  templateUrl: './card-ingredient.component.html',
  styleUrls: ['./card-ingredient.component.scss'],
})
export class CardIngredientComponent implements OnInit {
  @Input() ingredient: any;
  @Input() odd: any = false;
  @Input() even: any = false;
  constructor(public navCtrl: NavController,) { }

  ngOnInit() { }


  goToIngredient(ingredient: any, $event: any) {
    setTimeout(() => {
      let navigationExtras = {
        queryParams: {
          ingredient: JSON.stringify(ingredient)
        },
        animated: false
      };
      this.navCtrl.navigateForward(`ingredient/${ingredient.id}`, navigationExtras);
    }, 300);
  }
}
