import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class PageTransitionService {
  broadcast = new BehaviorSubject(false);
  constructor() { }

  emit(element) {
    if (!element) return;

    this.broadcast.next(element);
  }

  getBroadcastObservable() {
    return this.broadcast.asObservable();
  }

  public _generateDomBroadcast(container, activeImage) {

    const containerTop = container.getBoundingClientRect().top;
    const containerLeft = container.getBoundingClientRect().left;
    const containerHeight = container.getBoundingClientRect().height;
    const containerWidth = container.getBoundingClientRect().width;
    const containerSrc = '/assets/images/recipe-background.png';
    const containerBorderRadius = '1em';
    const activeImageSrc = activeImage.src;
    const activeImageTop = activeImage.getBoundingClientRect().top;
    const activeImageLeft = activeImage.getBoundingClientRect().left;
    const activeImageHeight = activeImage.getBoundingClientRect().height;
    const activeImageWidth = activeImage.getBoundingClientRect().width;
    const activeImageBorderRadius = '100%';

    return {
      background: { container: container, src: containerSrc, top: containerTop, left: containerLeft, height: containerHeight, width: containerWidth, borderRadius: containerBorderRadius },
      image: { container: activeImage, src: activeImageSrc, top: activeImageTop, left: activeImageLeft, height: activeImageHeight, width: activeImageWidth, borderRadius: activeImageBorderRadius }
    }
  }

  public _getDomInformationFromEvent(event) {

    const container = event.srcElement;
    const activeImage = container.querySelector(container.getAttribute('data-thumbnail-container'));
    activeImage.src = container.getAttribute('data-thumbnail')

    return this._generateDomBroadcast(container, activeImage);
  }

  public _getDomInformationFromRecipe(dom) {
    const container = dom.container;
    const activeImage = dom.activeImage;

    return this._generateDomBroadcast(container, activeImage);
  }
  getDomInformation(dom, event?: any) {

    if (event) {
      return this._getDomInformationFromEvent(event);
    } else {
      return this._getDomInformationFromRecipe(dom);
    }

  }
}
