import { Directive, ElementRef, HostListener, Input } from '@angular/core';
import { AnimationController, NavController } from '@ionic/angular';
import { fadeOut } from './../../animations/fade';
import { EventsService } from 'src/app/services/events.service';


@Directive({
  selector: '[growImageToPageHeader]'
})
export class GrowImageToPageHeaderDirective {
  @Input('goTo') goTo: string = "home";
  @Input('thumbnail') thumbnail: string = "";
  @Input('name') name: string = "";
  @Input('type') type: string = "recipe";
  @Input('data') data: any = {};

  @HostListener('click') onClick(event) {
    this.openTransition(this.element.nativeElement)
  }

  animCardBorderRadius: any;
  animCardPosition: any;
  animCardSize: any;
  animContainerBackgroundOpacity: any;
  div: any;

  constructor(public element: ElementRef, public animationCtrl: AnimationController, public navCtrl: NavController, public events: EventsService) { }

  ngAfterViewInit() {
    this.animCardBorderRadius = this.animationCtrl.create('animCardBorderRadius')
    this.animCardPosition = this.animationCtrl.create('animCardPosition')
    this.animCardSize = this.animationCtrl.create('animCardSize')
    this.animContainerBackgroundOpacity = this.animationCtrl.create('animContainerBackgroundOpacity');
  }

  openTransition(card) {


    // Get card background image and radius
    const backgroundImage = this.thumbnail || window.getComputedStyle(card).backgroundImage.slice(4, -1).replace(/"/g, "");
    const borderRadius = window.getComputedStyle(card).borderRadius


    // Get card initial measures
    const cardOffset = card.getBoundingClientRect();

    const cardWidth = cardOffset.width;
    const cardHeight = cardOffset.height;

    // Get card initial positions
    const distFromTop = cardOffset.top;
    const distFromLeft = cardOffset.left;

    // Insert a div with height: 100vh , width: 100vw , position fixed covering all page
    const div = document.createElement('div');
    div.style.position = "fixed";
    div.style.height = "100vh";
    div.style.width = "100vw";
    div.style.top = "0";
    div.style.right = "0";
    div.style.background = "rgba(255,255,255,0)";
    document.body.appendChild(div)

    // Insert proxy in div with the size of the card , backgroundImage, position, border radius and absolute 

    const proxyCard = document.createElement('div');
    proxyCard.style.position = "fixed";
    proxyCard.style.width = cardWidth + 'px';
    proxyCard.style.height = cardHeight + 'px';
    proxyCard.style.top = distFromTop + 'px';
    proxyCard.style.left = distFromLeft + 'px';
    proxyCard.style.borderRadius = borderRadius;
    proxyCard.style.background = 'url(' + backgroundImage + ')';
    proxyCard.style.backgroundPosition = "center";
    proxyCard.style.backgroundSize = "cover";


    div.appendChild(proxyCard)
    proxyCard.style.backgroundSize = "cover";

    this.animContainerBackgroundOpacity
      .addElement(div)
      .duration(300)
      .easing('ease-in')
      .iterations(1)
      .fromTo('background', 'rgba(255,255,255,0)', 'rgba(255,255,255,1)')
      .play();

    this.animCardBorderRadius
      .addElement(proxyCard)
      .duration(100)
      .easing('ease-in')
      .iterations(1)
      .fromTo('borderRadius', borderRadius, 0)
      .play();

    this.animCardSize
      .addElement(proxyCard)
      .duration(300)
      .easing('ease-in')
      .iterations(1)
      .fromTo('width', cardWidth, "100vw")
      .fromTo('height', cardHeight, "300px")
      .fromTo('top', distFromTop + 'px', "0px")
      .fromTo('left', distFromLeft + 'px', '0px')
      .play();

    this.div = div;
    this.events.subscribe('ON_PAGE_DID_ENTER', () => {
      this.removeElement(this.div);
      this.events.destroy('ON_PAGE_DID_ENTER');
    });
    setTimeout(() => {

      this.animCardSize.stop();
      this.animCardBorderRadius.stop();
      this.animContainerBackgroundOpacity.stop();

      let navigationExtras: any = {
        queryParams: {},
        animated: false
      }
      if (this.type === "recipe") {
        navigationExtras.queryParams.recipe = JSON.stringify(this.data);
        this.navCtrl.navigateForward(`recipe/${this.data.id}`, navigationExtras);
      } else {
        navigationExtras.queryParams.ingredient = JSON.stringify(this.data);
        this.navCtrl.navigateForward(`ingredient/${this.data.id}`, navigationExtras);
      }

    }, 1000)

  }

  async removeElement(div: any) {
    if (div) {
      await fadeOut(div);
      div.parentNode.removeChild(div);
      this.div = false;
    }
  }
}
