import { Component, ElementRef, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';
import { GestureController, Platform } from '@ionic/angular';
import { EventsService } from './../../services/events.service';


@Component({
  selector: 'app-drawer',
  templateUrl: './drawer.component.html',
  styleUrls: ['./drawer.component.scss'],
})
export class DrawerComponent implements OnInit {

  @ViewChild('drawer', { read: ElementRef }) drawer: ElementRef;
  @Output('openStateChanged') openState: EventEmitter<boolean> = new EventEmitter();

  isOpen = false;
  openHeight = 0;
  toggleSubscription: any;
  closeSubscription: any;

  constructor(public platform: Platform, public gestureController: GestureController, public events: EventsService) {
    this.toggleSubscription = events.subscribe('drawer:toggle', (data: any) => {
      this.toggleDrawer();
    });
    this.closeSubscription = events.subscribe('drawer:close', (data: any) => {
      this.closeDrawer();
    });
  }

  ngOnInit() { }

  ngOnDestroy() {
    this.toggleSubscription.unsubscribe();
    this.closeSubscription.unsubscribe();
  }

  toggleDrawer() {
    const drawer = this.drawer.nativeElement;

    if (this.isOpen) {
      drawer.style.transition = '.4s ease-out';
      drawer.style.transform = ``;
      this.isOpen = false
    } else {
      drawer.style.transition = '.4s ease-out';
      drawer.style.transform = `translateY(${-this.openHeight}px)`;
      this.isOpen = true
    }
  }

  closeDrawer() {
    const drawer = this.drawer.nativeElement;
    drawer.style.transition = '.4s ease-out';
    drawer.style.transform = ``;
    this.isOpen = false
  }

  async ngAfterViewInit() {
    const drawer = this.drawer.nativeElement;

    this.openHeight = (this.platform.height() / 100) * 70;
    const gesture = await this.gestureController.create({
      el: drawer,
      gestureName: 'swipe',
      direction: 'y',
      onMove: ev => {
        if (ev.deltaY < -this.openHeight) return;
        drawer.style.transform = `translateY(${ev.deltaY}px)`
      },
      onEnd: ev => {

        if (ev.deltaY < -50 && !this.isOpen) {
          drawer.style.transition = '.4s ease-out';
          drawer.style.transform = `translateY(${-this.openHeight}px)`;
          this.openState.emit(true);
          this.isOpen = true
        } else if (ev.deltaY > 50 && this.isOpen) {
          drawer.style.transition = '.4s ease-out';
          drawer.style.transform = ``;
          this.openState.emit(false);
          this.isOpen = false
        } else {
          drawer.style.transition = '.4s ease-out';
          drawer.style.transform = ``;
          this.openState.emit(false);
          this.isOpen = false
        }
      }
    });
    gesture.enable(true)
  }

}
