import {
  Component,
  OnInit,
  Input,
  SimpleChanges,
  ViewChild
} from "@angular/core";

@Component({
  selector: "app-nutritional-value",
  templateUrl: "./nutritional-value.component.html",
  styleUrls: ["./nutritional-value.component.scss"]
})
export class NutritionalValueComponent implements OnInit {
  @Input() value = "";
  @Input() title = "";
  @Input() percentage: any = "0";
  @Input() classes = "";
  @ViewChild("thumb") thumb: any;
  constructor() {}

  ngOnInit() {}

  ngOnChanges(changes: SimpleChanges) {
    let currentPercentage = this.percentage || "0";
    currentPercentage = String(currentPercentage);
    currentPercentage = parseInt(currentPercentage.trim("%"));

    try {
      this.thumb.nativeElement.style.strokeDasharray = `${(currentPercentage *
        250) /
        100} 999`;
    } catch (ex) {}
  }
}
