import { Component, OnInit, Input } from '@angular/core';
import { FavoritesStorage } from 'src/app/storage/favorites.storage';
import { EventsService } from './../../services/events.service';

@Component({
  selector: 'app-favorite',
  templateUrl: './favorite.component.html',
  styleUrls: ['./favorite.component.scss'],
})
export class FavoriteComponent implements OnInit {
  liked: boolean = false;
  icon: string = 'heart-outline';
  subscription: any;

  @Input('recipe') recipe: any = {}
  @Input('slot') slot: string = "icon-only"

  constructor(public favoritesStorage: FavoritesStorage, events: EventsService) {
    this.subscription = events.subscribe(`storage:${favoritesStorage.table}`, (data) => {
      this._checkState();
    });
  }

  public _setIcon() {
    if (this.liked) {
      this.icon = 'heart';
    } else {
      this.icon = 'heart-outline';
    }
  }

  public async _checkState() {
    let liked = false;
    const id = this.recipe.id;
    const favorites: any = await this.favoritesStorage.read();
    const ids = favorites.map(el => el.id);
    if (ids.indexOf(id) > -1) {
      liked = true;
      this.liked = true;
      this._setIcon();
      return;
    }
    this.liked = liked;
    this._setIcon();
    return;
  }

  toggleState($event: any) {
    $event.preventDefault();
    $event.stopPropagation();
    if (this.liked) {
      this.favoritesStorage.destroy(this.recipe.id);
    } else {
      this.favoritesStorage.create(this.recipe);
    }
  }

  ngOnInit() {
    this._checkState();
  }


  ngOnChanges() {
    this._checkState();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

}
